@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@1,300&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Mulish",sans-serif}.navbar-brand{font-weight:bold;font-size:1.3 rem}.active__class{color:#7171b8;font-size:1.4rem}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}#head1{color:#2d2dad}#header{margin:0 0 10px 0;font-size:48px;font-weight:700;color:#000}.header-img{text-align:right}.anomated{animation:up-down 2s ease-in-out infinite alternate-reverse both}.navbar{background-color:#fff}footer{position:relative;left:0;bottom:0;width:100%;color:#000;text-align:center}.box{width:100%;height:100%;display:flex;justify-items:center;flex-direction:column}
